<template>
    <div>
        <el-row :gutter="20" class="mgb20">
            <el-col :span="8">
                <el-card class="card">
                    <div class="grid-content">
                        <div class="grid-cont-center">
                            <div class="grid-num">{{consumerCount}}</div>
                            <div>{{$t('common.consumerCount')}}</div>
                        </div>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="8">
                <el-card class="card">
                    <div class="grid-content">
                        <div class="grid-cont-center">
                            <div class="grid-num">{{bookCount}}</div>
                            <div>{{$t('common.bookCount')}}</div>
                        </div>
                    </div>
                </el-card>
            </el-col>
            <el-col :span="8">
                <el-card class="card">
                    <div class="grid-content">
                        <div class="grid-cont-center">
                            <div class="grid-num">{{writerCount}}</div>
                            <div>{{$t('common.writerCount')}}</div>
                        </div>
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <el-row :gutter="20" class="mgb20">
            <el-col :span="12" >
                <h3 class="mgb20 txt">{{$t('common.t1')}}</h3>
                <div style="background-color:white" class="p20 card">
                    <ve-pie :data="consumerSex" :theme="options"></ve-pie>
                </div>
            </el-col>
            <el-col :span="12">
                <h3 class="mgb20 txt">{{$t('common.t2')}}</h3>
                <div style="background-color:white" class="p20 card">
                    <ve-histogram :data="bookStyle"></ve-histogram>
                </div>
            </el-col>
        </el-row>
        <el-row :gutter="20" class="mgb20">
            <el-col :span="12">
                <h3 class="mgb20 txt">{{$t('common.t3')}}</h3>
                <div style="background-color:white" class="p20 card">
                    <ve-ring
              :legend-visible="true"
              :extend="sexExtend"
              :resizeable="true"
              :settings="sexSettings"
              :data="sexData"
            />
                </div>
            </el-col>
            <el-col :span="12">
                <h3 class="mgb20 txt">{{$t('common.t4')}}</h3>
                <div style="background-color:white" class="p20 card">
                    <ve-histogram :data="country" :theme="options1"></ve-histogram>
                </div>
            </el-col>
        </el-row>
        <el-row :gutter="20" class="mgb20">
            <el-col :span="12" >
                <h3 class="mgb20 txt">{{$t('common.t5')}}</h3>
                <div style="background-color:white" class="p20 card">
                    <ve-pie :data="year"></ve-pie>
                </div>
            </el-col>
            <el-col :span="12" >
                <h3 class="mgb20 txt">{{$t('common.t6')}}</h3>
                <div style="background-color:white" class="p20 card">
                    <ve-bar class="barStyle"
            :data="areaData"
            :legend-visible="false"
            :colors="colors"
            :settings="areaSettings"
            :extend="areaExtend"
            :grid="{ containLabel: true, right: 40, top: 30, left: 60 }"
          />
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import {getAllConsumer,getAllBook,getAllWriter} from '../api/index';
export default {
    data(){
        this.sexSettings = {
            dimension: "talkDuration",
            metrics: "总数",
            // radius: ["60px", "100px"],  //圆环内径和外径，可以用px单位
            radius: ["40%", "65%"],  //之所以注释掉换成百分比，是为了做屏幕适配，使图表在大屏小屏电脑上能自动变大和变小
        };
        this.sexExtend = {
            legend: {
            show: true,  //显示图例
            orient: "horizontal",  //使图例竖向排列
            icon: "circle",  //图例的标识改成圆点
            bottom: 0,  //距离底部0
            itemGap: 20,  //图例各个元素之间的距离为20
            formatter: (name) => {   //自定义图例上的文字格式
                let param = "";
                if (this.sexData) {
                    param = this.sexData.rows.find((item) => {
                    return item["talkDuration"] === name;
                    })["总数"];
                }
            return name + "   " + param + "   " ;
            },
            },
            series: {
            center: ["50%", "35%"],  //图表在容器的位置
            label: {
                normal: {
                    show: false,
                },
            },
            },
        color: ["#FA6090", "#5253FD", "#05C8D6","yellow"],  //改变环形每个区域的颜色
        };
        this.areaSettings = {
            dimension: ["talkDuration"],
            metrics: ["总数"],
            dataOrder: {   //按照由大到小进行排序
            label: "总数",
            order: "desc",
            },
            itemStyle: {
            normal: {   //这里没有使用百分数的单位，所以%是自己加上去的
                label: { show: true, position: "right",},
            },
            },
            max: [100, 100],
        };
        this.areaExtend = {
            "yAxis.0.splitLine.show": false, // y轴表格线不显示
            "xAxis.0.splitLine.show": false, // x轴标签不显示
            "xAxis.0.axisLabel.show": false, // x轴标签不显示
            yAxis: {
            axisLabel: {
            textStyle: {
                color: "#9B9B9B", // y轴字体颜色
                padding: [10, 15, 5, 6], // 上右下左
            },
            },
        },
      // 每个柱子
        series(v) {
        // console.log("v", v);
        // 设置柱子的样式
            v.forEach((i) => {
                i.barWidth = 45;  //柱子高度
            });
            return v;
        },
        };
        return {
            consumerCount: 0,       //用户总数
            bookCount: 0,           //书籍总数
            writerCount: 0,         //作家总数
            consumer: [],           //所有用户
            consumerSex:{           //按性别分类的用户数
                columns: ['性别','总数'],
                rows: [
                    {'性别':'男','总数': 0},
                    {'性别': '女','总数': 0}
                ]
            },
            year:{
                columns: ['年份','总数'],
                rows: [
                    {'年份':'50后','总数': 0,'标准': '195'},
                    {'年份':'60后','总数': 0,'标准': '196'},
                    {'年份':'70后','总数': 0,'标准': '197'},
                    {'年份':'80后','总数': 0,'标准': '198'},
                    {'年份':'90后','总数': 0,'标准': '199'},
                    {'年份':'00后','总数': 0,'标准': '200'},
                    {'年份':'10后','总数': 0,'标准': '201'},
                    {'年份':'其他','总数': 0},
                ]
            },
            options: {
                color: ['#87cefa','#ffc0cb']
            },
            options1: {
                color: ['#006600']
            },
            bookStyle:{           //按书籍风格分类
                columns: ['风格','总数'],
                rows: [
                    {'风格': '武侠仙侠','总数': 0},
                    {'风格': '穿越小说','总数': 0},
                    {'风格': '历史军事','总数': 0},
                    {'风格': '现代言情','总数': 0},
                    {'风格': '都市生活','总数': 0},
                    {'风格': '悬疑科幻','总数': 0},
                    {'风格': '宫斗权谋','总数': 0},
                    {'风格': '奇幻玄幻','总数': 0},
                  {'风格': '','总数': 0}
                ]
            },
            country:{
                columns: ['国籍','总数'],
                rows: [
                    {'国籍': '上海','总数': 0},
                    {'国籍': '遂宁','总数': 0},
                    {'国籍': '成都','总数': 0},
                    {'国籍': '重庆','总数': 0},
                    {'国籍': '北京','总数': 0},
                    {'国籍': '浙江','总数': 0},
                    {'国籍': '广东','总数': 0},
                    {'国籍': '厦门','总数': 0}
                ]
            },
            sexData: {
                rows: [
                    { 'talkDuration': '女', '总数': 0 },
                    { 'talkDuration': '男', '总数': 0 },
                    { 'talkDuration': '组合', '总数': 0 },
                    { 'talkDuration': '不明', '总数': 0 }
                ],
            },
            colors: ["#CC00FF"],
            areaData: {
                rows: [
                    { 'talkDuration': '50后', '总数': 0 ,'标准': '195'},
                    { 'talkDuration': '60后', '总数': 0 ,'标准': '196'},
                    { 'talkDuration': '70后', '总数': 0 ,'标准': '197'},
                    { 'talkDuration': '80后', '总数': 0 ,'标准': '198'},
                    { 'talkDuration': '90后', '总数': 0 ,'标准': '199'},
                    { 'talkDuration': '00后', '总数': 0 ,'标准': '200'},
                    { 'talkDuration': '10后', '总数': 0 ,'标准': '201'},
                    { 'talkDuration': '其他', '总数': 0 },
                ],
            },
        }
    },
    mounted() {
        this.getConsumer();
        this.getBook();
        this.getWriter();
    },
    methods: {
        getConsumer() {                     //用户总数
            getAllConsumer().then(res => {
                this.consumer = res;
                this.consumerCount = res.length;
                this.consumerSex.rows[0]['总数'] = this.setSex(1,this.consumer);
                this.consumerSex.rows[1]['总数'] = this.setSex(0,this.consumer);
                for(let item of res){
                    this.getYear(item.birth.slice(0,4));
                }
                this.year.rows[7]['总数'] -= res.length * 7;
            })
        },
        getYear(birth){
            for(let item of this.year.rows){
                if(birth.includes(item['标准'])){
                    item['总数']++;
                }else{
                    this.year.rows[7]['总数']++;
                }
            }
        },
        getYears(birth){
            for(let item of this.areaData.rows){
                if(birth.includes(item['标准'])){
                    item['总数']++;
                }else{
                    this.areaData.rows[7]['总数']++;
                }
            }
        },
        setSex(sex,val) {              //根据性别获取用户数
            let count = 0;
            for(let item of val){
                if(sex == item.sex){
                    count++;
                }
            }
            return count;
        },
        getBook() {                      //书籍总数
            getAllBook().then(res => {
                this.bookCount = res.length;
                for(let item of res){
                    this.getByStyle(item.style);
                }
            })
        },
        getWriter() {                     //作家总数
            getAllWriter().then(res => {
                this.writerCount = res.length;
                this.sexData.rows[0]['总数'] = this.setSex(0,res);
                this.sexData.rows[1]['总数'] = this.setSex(1,res);
                this.sexData.rows[2]['总数'] = this.setSex(2,res);
                this.sexData.rows[3]['总数'] = this.setSex(3,res);
                for(let item of res){
                    this.getByCountry(item.location);
                    this.getYears(item.birth.slice(0,4));
                }
                this.areaData.rows[7]['总数'] -= res.length * 7;
            })
        },
        getByStyle(style) {              //根据书籍风格获取数量
            for(let item of this.bookStyle.rows){
                if(style.includes(item['风格'])){
                    item['总数']++;
                }
            }
        },
        getByCountry(location) {              //根据国籍获取数量
            for(let item of this.country.rows){
                if(location.includes(item['国籍'])){
                    item['总数']++;
                }
            }
        },
    }
}
</script>

<style scoped>
.grid-content {
    display: flex;
    align-items: center;
    height: 50px;
}

.grid-cont-center {
    flex: 1;
    text-align: center;
    font-size: 14px;
    color: darkgray;
}

.grid-num {
    font-size: 30px;
    font-weight: bold;
}
.card{
    border: none;
}
.dark .card{
    background-color: #141414 !important;
}
.dark .txt{
    color: #A9A9A9;
}
.p20{
    padding: 20px;
}
</style>



